/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

body {
  font: 16px/28px "Gotham Pro";
  color: #333333;
  background: #f8f6f3;
}

.site-header {
  background: url(../img/header-paper.png) top center no-repeat;
  padding-bottom: 70px;
}

.large-container {
  padding: 0 110px;
  position: relative;
}

.large-container__inner {
  position: relative;
}

.container {
  max-width: 1160px;
  margin: auto;
  padding: 0 10px;
}

.main-menu {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  margin: 35px 0 0;
  padding: 0;
  list-style-type: none;
}

.main-menu a {
  text-transform: uppercase;
  color: #333333;
  font: 18px "bebas";
  letter-spacing: 0.1em;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.main-menu a:hover {
  border-color: #77d5ad;
}

.logo {
  position: absolute;
  left: 0;
  top: 20px;
  z-index: 2;
}

.anonses-link {
  position: absolute;
  right: 0;
  top: 20px;
  display: block;
  width: 180px;
  height: 50px;
  background-color: #ffe578;
  border-radius: 2px;
  color: #333333;
  font: 20px/50px "bebas";
  letter-spacing: 0.1em;
  text-align: center;
  text-decoration: none;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.anonses-link:hover {
  background-color: #f9d642;
}

.header-slider {
  margin-top: 40px;
}

.header-slider .slick-slide {
  position: relative;
}

.header-slider .slick-dots {
  margin: 60px 0 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}

.header-slider .slick-dots li {
  display: inline-block;
  margin: 0 7px;
  width: 8px;
  height: 8px;
}

.header-slider .slick-dots li button {
  font-size: 0px;
  text-indent: -9999px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 1px solid #4c4a55;
  display: block;
  background: transparent;
  padding: 0;
  cursor: pointer;
}

.header-slider .slick-dots li button:focus {
  outline: 0;
}

.header-slider .slick-dots li.slick-active button {
  background: #4c4a55;
}

.header-slider__quote {
  font: 32px "lazy";
  color: #3f0da4;
  float: right;
  margin: 0 270px 50px 0;
}

.header-slider__quote span {
  font: 300 18px "Gotham Pro";
  display: block;
  text-align: center;
  margin-top: 5px;
}

.heading__img img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
}

.anons__heading {
  font: 500 36px "Gotham Pro";
  color: #44424d;
  text-transform: uppercase;
  text-align: center;
  margin: 15px 0 40px;
  letter-spacing: 0.1em;
}

.watch-trailer {
  width: 258px;
  height: 50px;
  background-color: #9ccc65;
  border-radius: 2px;
  display: block;
  margin: auto;
  text-align: center;
  color: #fff;
  font: 700 20px/52px "bebas";
  text-decoration: none;
  letter-spacing: 0.1em;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.watch-trailer span {
  padding-right: 26px;
  background: url(../img/watch-ico.png) right center no-repeat;
}

.watch-trailer:hover {
  background: #8abb51;
}

